@import url('https://fonts.googleapis.com/css2?family=EB+Garamond:ital,wght@0,400..800;1,400..800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Caveat:wght@400..700&family=Dancing+Script:wght@400..700&family=Libre+Baskerville:ital,wght@0,400;0,700;1,400&family=Sour+Gummy:ital,wght@0,100..900;1,100..900&display=swap');

:root{
    --font_weigth: 700; 
}

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body{
    min-height: 100vh;
    display: grid;
    place-content: center;
    text-align: center;
    font-family: "Libre Baskerville", serif;
    overflow: hidden;
    /* background-color: #eaeaea; */
    background-image: url('./assests/imgs/bg01.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    height: auto;
    width: 100%;

    cursor: url('https://icons8.com/icon/12944/pen');
    opacity: 1;
    animation: onLoadAnim  3s; 
    animation-fill-mode: forwards;
    
    /* background-image: url(/assests/imgs/Catalogo\ Fidaty\ Esselunga.jpg);
    background-repeat: no-repeat;
    height: 100%;
    width: 100%;
    background-size: cover; */
    /* cursor: url('./assets/images/cursor.png'), auto; */
}

.contents{
    margin-bottom: 30px;
    padding-bottom: 4em;
    display: flex;
    justify-content: center;
    padding-left: 25em;
    width: 100%;
    height: auto;
    position: relative;
}


.contents > *{
    margin: 0 auto;
}

