.info{
    position: relative;
    gap: 1rem;
    padding: 2px 15px 15px;
    align-items: center;
    margin-bottom: 1em;
    margin-top: 4em;
    margin-left: 1em;
}

.info > *{
    margin: 0;
}

.contents .user-pro{
    position: fixed;
    max-width: 100%;
    height: auto;
    display: block;
    /* border-radius: 50%; */

    margin-right: 70em;
   
}
#typed-name{
    display: inline-block;
    font-weight: 100;
    margin-right: 5em;
    font-size: 48px;
    line-height: 69px;
    color: rgb(63, 62, 62);
    margin-top: 10px;
    padding-bottom: .240rem;
    text-align: left;
}

#name{
    background-color: #D6D0FF;
    font-weight: var(--font_weigth);
}

#designer{
    color: #F76CFE;
    font-family: "Caveat", cursive;
    font-weight: var(--font_weigth);

}

#reader{
    color: #a397ee;
    font-family: "Caveat", cursive;
    font-weight: var(--font_weigth);


}
#cine{
    color: #0000DC;
    font-family: "Caveat", cursive;
    font-weight: var(--font_weigth);
}

#typed{
    display: inline-block;
}
.typed-cursor{
font-weight: 900;
  box-shadow: 0px 1px;
  font-size: 1.3em;
}


.user-about{
    position: relative;
    font-size: 15px;
    text-align: left;
    margin-top: 1em;
    justify-content: space-around;
    text-justify: auto;
    overflow-wrap: break-word;
    width: 44em;
    line-height: 2em;
    color: #6c6767;
    /* animation: onLoadAnim 10s;
    transition: ease 14s; */
}

.socialsBtn > *{
    margin: 0;
}

/* From Uiverse.io by Voxybuns */ 
.socialsBtn{
    position: fixed;
    padding-bottom: 10rem;
    padding-top: 4em;
    margin-left: 7em;
    
}

.dribble, .linkedin, .github{
    width: 40px;
    height: 40px;
}
.email{
    width: 39px;
    height: 39px;
}
.dribble{
    width: 39px;
    height: 39px;
}
.linkedin{
    width: 39px;
    height: 39px;
}
.button{

    --button_radius: 0.75em;
    --button_color: #e8e8e8;
    --button_outline_color: #fec76f;
    border-radius: var(--button_radius);
    position: relative;
    gap: 20px;
    margin-left: 2em;    
    -webkit-transform: rotate(15deg) scale(1.4);
    transform: rotate(15deg) scale(1.4);
    -webkit-transition: .3s ease-in-out; 
    transition: .2s ease-in-out;
}

.button:hover{
    -webkit-transform: rotate(0) scale(1);
    transform: rotate(0) scale(1);

}

a:link{
    text-decoration: none;
}



@keyframes onLoadAnim{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}



/* media queries 
 */

 /* Base styles are already defined above */

@media (max-width: 768px) {
    body {
        place-content: flex-start; /* Align content at the top */
        overflow: auto; /* Prevent content clipping */
        padding: 10px;
    }

    .contents {
        flex-direction: column;
        padding: 0; /* Remove excessive padding */
        margin: 0 auto;
    }

    .contents .user-pro {
        margin: 0 auto;
        position: relative; /* Remove fixed positioning */
        max-width: 90%; /* Adjust width for smaller screens */
    }

    #typed-name {
        font-size: 24px;
        line-height: 36px;
        margin: 0 auto;
        padding-bottom: 1rem;
    }

    .user-about {
        width: 90%;
        margin: 1rem auto;
        font-size: 14px;
        line-height: 1.6em;
    }

    .socialsBtn {
        padding: 1rem 0;
        margin: 0 auto;
        display: flex;
        justify-content: center;
        gap: 10px;
    }

    .button {
        transform: rotate(0) scale(1.2); /* Adjust button size for smaller viewports */
        margin: 0 auto;
    }

    .dribble, .linkedin, .github, .email {
        width: 30px;
        height: 30px; /* Adjust icon sizes */
    }

    #typed {
        font-size: 29px; /* Adjust typed text size */
    }
}

@media (max-width: 480px) {
    #typed-name {
        font-size: 33px;
        line-height: 28px;
    }

    .user-about {
        font-size: 10px;
        width: 100%;
    }

    .contents .user-pro {
        max-width: 90%;
    }

    .button {
        transform: scale(1); /* Adjust button for smaller screens */
    }
}
